import React from "react"
import Logo from "../components/logo"
import Nav from "../components/navigation"
import SEO from "../components/seo"
import Kitchen from "../components/kitchen"
import PageInfo from "../components/pageinfo"
import GiantHeader from "../components/giantheader"
import { LIVE_INFO } from "../components/siteinfo"
import { LOGO_COLOR_DARK, MAIN_TEXT_COLOR } from "../components/siteconfig"
import 'aos/dist/aos.css';


class Live extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      isActive: true
    }
  }
  componentDidMount() {
    const AOS = require('aos');
    this.aos = AOS
    this.aos.init()
  }

  componentDidUpdate() {
    this.aos.refresh()
  }
  render() {
    return (
      <div style={{ display: 'flex', maxWidth: `100%`, height: `2000px` }}>
        <SEO title="Live - 1240 Greene St" />
        <div css={{ zIndex: '10', width: '50%' }}>
          <Logo title="1240 Greene St" color={LOGO_COLOR_DARK} />
          <div data-aos="fade-right" data-aos-delay="300" data-aos-easing="ease-in-sine" data-aos-duration="500">
            <PageInfo header={LIVE_INFO.header} decscription={LIVE_INFO.description} color={MAIN_TEXT_COLOR} marginTop="18%" />
          </div>
        </div>

        <div css={{ zIndex: `100`, marginLeft: `auto`, justifyContent: `flex-end` }}>
          <div>
            <Nav isActive={this.state.isActive} />
          </div>
          <div css={{
            position: 'fixed',
            bottom: '-119px',
            left: '-41%',
            transform: 'scale(.45)',
            '@media(min-width:700px)': {
              bottom: '-67px',
              transform: 'scale(1)',
              left: '60%'
            }
          }}>
            <div data-aos="fade-left" data-aos-delay="350" data-aos-duration="500">


              <GiantHeader text="Live" color="#fff" opacity=".4" stroke="1px #000" />
            </div>
          </div>
        </div>


        <div css={{ position: 'absolute', top: '0', left: '0', width: '100%', height: '2000px' }}>
          <Kitchen />
        </div>

      </div>
    )
  }
}


export default Live
